import _ from 'lodash';
import uniqid from 'uniqid';
import moment from 'moment';
import Player from '../../../models/Player';
import { EventOdd } from '../../../models/EventOdd';
import { OpenBet, HistoryBet } from '../../../models/BetOrder';

import compareBetDetail from '../../../utils/compareBetDetail';
import updatePlayerStatusAfterOrder from '../../../updateDB/utils/updatePlayerStatusAfterOrder';


const submitSingleBetOrder = async (req, res) => {

	const { eventOdds, wagerDetails, orderType } = req.body
	Promise.all(eventOdds.map(async (event, eventIdx) => {
		const eventTimeOut = moment().isSameOrAfter(moment(event.cutOffTime).format())
		const existed = await OpenBet.findOne({ 'owner.player': req.user._id, orderType: orderType, 'eventOdds.singlePickId': event.singlePickId })
		switch(true){
			case eventTimeOut:
				event.status = 'TimeOut'
				delete event.betDetail
				break;
			case !_.isEmpty(existed):
				event.status = 'Existed'
				event.note = existed.orderNumber
				delete event.betDetail
				break;
			default:
				const latestEvent = await EventOdd.findOne({ eventOddId: event.eventOddId })
				if(compareBetDetail(event, latestEvent, orderType).betDetailMatch){
					event.status = 'Pending'
					event.note = ''
				}else{
					event.status = 'HasUpdated'
					event.betDetailPrev = event.betDetail
					event.betDetail = compareBetDetail(event, latestEvent, orderType).latestBetDetail
				}
				return
		}
		if(event.status !== 'Pending'){
			wagerDetails[event.singlePickId] = {
				betType: 'wager',
				betAmount: 0,
				riskAmount: 0,
				winAmount: 0,
				confirm: false
			}
		}
		return event
	})).then(async () => {
		const submitCheckPass = eventOdds.every(event => event.status === 'Pending')
		if(!submitCheckPass){
			res.json({ eventOdds: eventOdds, wagerDetails: wagerDetails })
		}else{
			await Promise.all(eventOdds.map(async event => {
				const newOpenBet = new OpenBet({
					orderNumber: uniqid.process().toUpperCase(),
					orderType: orderType,
					owner:{
						player: req.user._id,
						agent: req.user.agent,
						superAgent: req.user.superAgent
					},
					wagerDetail: {
						betType: wagerDetails[event.singlePickId].betType,
						betAmount: wagerDetails[event.singlePickId].betAmount,
						winAmount: wagerDetails[event.singlePickId].winAmount,
						riskAmount: wagerDetails[event.singlePickId].riskAmount
					},
					status: 'Pending',
					eventOdds: [event]
				})
				await newOpenBet.save().then(() => { console.log(`saved new openBet ${newOpenBet.orderNumber}`) }).catch(err => { throw err })
				return null
			})).then(async () => {
				await updatePlayerStatusAfterOrder(req.user._id)
				res.json([])
			}).catch(err => { throw err })
		}
	}).catch(err => { throw err })
}

export default submitSingleBetOrder;























